<template>
  <div id="app">
    总得分:{{ totalScore }}
    <div class="top">
      <div
        class="card"
        v-for="(item, index) in allCard"
        :key="index"
        :style="{ top: item.top, left: item.left }"
        @touchstart="select(item, index, $event)"
      >
        <span :class="'iconfont ' + item.icon"></span>
      </div>
    </div>
    <div class="botton">
      <div class="bot-card" v-for="(item, index) in selectCard" :key="index">
        <span :class="'iconfont ' + item.icon"></span>
      </div>
    </div>
    <button class="restart" @click="restart">重新开始</button>
    <button class="restart" @click="zdy">自定义</button>
    <div class="mask" v-if="isMask">
      {{ result }}
      <button @click="restart">重新开始</button>
    </div>
    <div class="mask" v-if="isZdy">
      <ul>
        <li>卡片种类:<input max="10" type="number" v-model="kind" /></li>
        <li>卡片组数:<input type="number" v-model="num" /></li>
        <li><button @click="verifyZdy">确认</button></li>
      </ul>
      
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  mounted() {
    this.createCard();
  },
  data() {
    return {
      // 自定义卡片种类数量
      kind:0,
      // 自定义卡片组数
      num:0,
      // 结果
      result: "您赢了!",
      // 结果蒙版显示隐藏
      isMask: false,
      // 自定义蒙版
      isZdy: false,
      // 总分
      totalScore: 0,
      // 全部卡片
      allCard: [],
      // 卡片随机位置
      cardLocation: [],
      // 选中的卡片
      selectCard: [],
      // 卡片图标
      icon: [
        { icon: "icon-shafaB" },
        { icon: "icon-yiziB" },
        { icon: "icon-dayinjiB" },
        { icon: "icon-xiangjiB" },
        { icon: "icon-dianfengshanB" },
        { icon: "icon-dianfanbaoB" },
        { icon: "icon-shouyinjiB" },
        { icon: "icon-chuifengjiB" },
        { icon: "icon-yupenB" },
        { icon: "icon-dengpaoB" },
      ],
    };
  },
  methods: {
    // 点击卡片
    select(item, index, event) {
      let isSelect = this.cardStress(event.srcElement);
      if (!isSelect) {
        this.allCard.splice(index, 1);
        this.selectCard.push(item);
        let num = this.selectCard.reduce((n, v) => {
          return item.icon == v.icon ? n + 1 : n;
        }, 0);
        if (num == 3) {
          setTimeout(() => {
            this.totalScore++;
            this.selectCard = this.selectCard.filter((v) => {
              return item.icon !== v.icon;
            });
          }, 200);
        }
        if (this.selectCard.length === 7) {
          this.result = "您失败了!";
          this.isMask = true;
        }
        if(this.allCard.length===0){
          this.isMask = true;
          this.result = "您赢了!"
        }
      }
    },
    // 生成卡片位置
    createCardLocation() {
      let top = 0;
      let left = 0;
      for (let i = 0; i < 7; i++) {
        for (let j = 0; j < 7; j++) {
          this.cardLocation.push({
            top: top + "rem",
            left: left + "rem",
          });
          left += 48;
        }
        top += 48;
        left = 0;
      }
      top = 24;
      left = 24;
      for (let i = 0; i < 6; i++) {
        for (let j = 0; j < 6; j++) {
          this.cardLocation.push({
            top: top + "rem",
            left: left + "rem",
          });
          left += 48;
        }
        top += 48;
        left = 24;
      }
    },
    // 生成随机卡片
    createCard(kind=10,num=300) {
      this.createCardLocation();
      let icon;
      for (let i = 0; i < num; i++) {
        if (i % 3 == 0) {
          icon = this.icon[Math.floor(Math.random() * kind)].icon;
        }
        this.allCard.push({
          id: i,
          top: this.cardLocation[Math.floor(Math.random() * 85)].top,
          left: this.cardLocation[Math.floor(Math.random() * 85)].left,
          icon,
        });
      }
    },
    // 压住禁止点击判断
    cardStress(Dom) {
      let document = Dom.ownerDocument,
        rect = Dom.getBoundingClientRect(),
        w = rect.width,
        h = rect.height,
        x = rect.x,
        y = rect.y;
      let elements = [
        document.elementFromPoint(x + 1, y + 1),
        document.elementFromPoint(x + w - 1, y + 1),
        document.elementFromPoint(x + 1, y + h - 1),
        document.elementFromPoint(x + w - 1, y + h - 1),
      ];
      return elements.filter((el) => el != null).some((el) => el !== Dom);
    },
    // 重新开始
    restart() {
      this.allCard = [];
      this.selectCard = []
      this.totalScore = 0;
      this.isMask = false;
      this.createCard();
    },
    // 自定义
    zdy(){
      this.isZdy = true

    },
    verifyZdy(){
      this.allCard = []
      this.selectCard = []
      this.totalScore = 0
       this.createCard(this.kind,this.num*3)
       this.isZdy = false
    }
  },
};
</script>

